import "./style/style.css";
export function narcissistic(value) {
    // your code here
    var str = value.toString();
    var strLength = str.split("").length;
    var total = 0;
    for (var i = 0; i < strLength; i++) {
        var result = Math.pow(parseInt(str[i]), strLength);
        total += result;
    }
    return total === value;
}
console.log(narcissistic(153));
console.log(narcissistic(1634));
